import { Component } from '@angular/core';
import {StringUtils} from './_utils/string-utils';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  text: string;
  subText: string;
  results: number [] = [];

  constructor() {
  }

  check() {
    if (this.text && this.subText) {
      this.results = StringUtils.indexOfIgnoreCase2(this.text, this.subText);
    }
  }
}



