export class StringUtils {
  public static indexOf(str: string, subStr: string): number[] {
    const start = new Date().getMilliseconds();
    const positions: number [] = [];
    for (let i = 0; i < str.length; i++) {
      let index = 0;
      while ((i + index) < str.length &&  str.charAt(i + index) === subStr.charAt(index)) {
        if (index === subStr.length - 1) {
            positions.push(i + 1);
            break;
        }
        index++;
      }
    }
    console.log( new Date().getMilliseconds() - start);
    return positions;
  }

  public static indexOfIgnoreCase(str: string, subStr: string): number[] {
    return StringUtils.indexOf(str.toLowerCase(), subStr.toLowerCase());
  }

  public static indexOf2(str: string, subStr: string): number[] {
    const start = new Date().getMilliseconds();
    const positions: number [] = [];
    let subIndex = 0;
    for (let i = 0; i < str.length; i++) {
      if (str.charAt(i) === subStr.charAt(subIndex)) {
        subIndex ++;
      }else {
        subIndex = 0;
      }
      if (subIndex === subStr.length) {
        positions.push(i - subIndex + 2);
        subIndex = 0;
      }
    }
    console.log( new Date().getMilliseconds() - start);
    return positions;
  }

  public static indexOfIgnoreCase2(str: string, subStr: string): number[] {
    return StringUtils.indexOf2(str.toLowerCase(), subStr.toLowerCase());
  }


}



